*{
    color: whitesmoke;
    
}

/* CSS that formats the column text in the center of the screen*/
.centerDiv{
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    align-items: center;
    
    padding: 1rem;
}

.centerDiv h1, h2{
    color: var(--primary-green);
    padding: 10px;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 1rem;
    
}

.centerDiv ul{
    width: 70%;
    list-style-position: outside;

}

.centerDiv p, span{
    width: 60%;

}

.centerDiv b{
    color: var(--secondary-purple);
}

.centerDiv a{
    font-weight: bold;
    text-decoration: none;
}

.centerDiv a:hover{
    font-weight: bold;
    text-decoration: underline;
}

/* CSS that creates a horizontal divider with text in the center*/
.customDivider{
    display: flex;
    align-items: center;
    text-align: center;
    width: 75%;
}

.customDivider::before,
.customDivider::after {
    content: '';
    flex: 1;
    border-bottom: 2px double var(--primary-green);
    
}

/*Specific Link Colors*/
.genericLinkColor{
    font-size:x-large;
    color: var(--primary-purple);
}

.inLineLink{
    color: var(--primary-purple);
}

.splatterbotLinkColor{
    font-size:x-large;
    color: var(--splatterbot-color);
    
}

.satelliteLinkColor{
    font-size:x-large;
    color: var(--satellite-color);
}

.mainContent{
    padding: 200px;
}

.outlineBox{
    background-color: var(--bg-color);
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    align-items: center;
    
    padding: 1rem;
    width: 65%;
    -moz-border-radius: 50px;
    -webkit-border-radius: 50px;
    border-radius: 50px;
    outline-style: solid;
    outline-color: var(--splatterbot-color);
    
}

div.scroll-container {
    background-color: #333;
    display: flex;
    
    align-items: center;
    overflow: scroll;
    overflow-y: hidden;
    white-space: nowrap;
    padding: 10px;

    height: 700px;
    width: 70%;
  }
  
div.scroll-container img {
    padding: 10px;
}

.galleryHorizontalImage{
    height: 400px;
    width: 600px;
}
.galleryVerticalImage{
    height: 600px;
    width: 400px;
}