* {
    box-sizing: border-box;
}

body {
    margin: 0;
    background-color: var(--bg-color);
}

.nav{
    color: whitesmoke;
    display: flex;
    flex-flow: column wrap;
    justify-content: center;
    align-items: center;
    
    position: fixed;

    padding: 0 1rem;
    background-color: var(--bg-color);
    border-color: var(--bg-green);
    border-bottom: 5px solid var(--primary-green);
    width: 100%;
}

.nav h1{
    color: var(--primary-green);
}

.nav ul {
    padding: 10px;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 1rem;

    display: flex;
    justify-content: center;
    align-items: center;
}

.nav a {
    color:inherit;
    text-decoration: none;
    display: flex;
    gap: .25rem;
}

.nav li.active{
    color: var(--satellite-color);
    font-weight: bold;
}

.nav li:hover{
    color: var(--satellite-color);
    font-weight: bold;
}

